<script>
import * as $ from "jquery";



export default {
  name: "Questionnaire",

  data () {
    return {
      disable: false,
      count : 0,
      data : {
        'data' : [],
        'store_id': this.$store.getters.store.id
      },
      name:"",
      phone_number: "",
      delivery_issue: "",
      delivery_speed: "",
      order_tampered: "",
      recommend_web: "",
      recommend_org: "",
      meet_needs: "",
      comments: "",
      web_satisfaction: "",
      share_contact: "",
      concern_resolution: "",
      dispatch_rude: "",
      trust_web: "",
      understand_web: "",
      appealing: "",
      find_time: "",
      find: "",
    }
  },

  mounted() {
    // if(localStorage.getItem("CompletedQuestionnaire") != "true" && localStorage.getItem("QuestionnaireSeen") < 2) {
    //   $('#exampleModalLong').modal('show');
    // }
    // if (localStorage.getItem("QuestionnaireSeen") != 0 && !isNaN(localStorage.getItem("QuestionnaireSeen"))){
    //   localStorage.QuestionnaireSeen = Number(localStorage.QuestionnaireSeen) + 1;
    // } else {
    //   localStorage.QuestionnaireSeen = 1;
    // }

    // if(this.$store.getters.isLoggedIn) {
    //   this.phone_number = this.$store.getters.user.phone;
    //   this.name = this.$store.getters.user.first_name + this.$store.getters.user.first_name;
    // }


    // setTimeout(() => this.disable = true, 30000)
  },

  computed: {
    notDisabled() {
      return this.disable
    }, 
    user_id(){
      return this.$store.getters.isLoggedIn ? this.$store.getters.user.id : null
    }
  },

  methods: {
    log() {
      console.log(this.count++);
    },
    selectNum(el) {
      $('.answer-label').each((index, element) => {
        $(element).removeClass('active');
      });
      $(`label[for='${el}']`).addClass('active');
    },
    check() {
      localStorage.CompletedQuestionnaire = "true";
      $('#exampleModalLong').modal('hide');
    },
    submit() {
      console.log('submitting');
      let data = [];
      let individual = {};
      document.querySelectorAll(".office-form-question").forEach(function(el) {
            individual.questiontext = el.querySelector(".office-form-question-title").querySelector("span:nth-child(2)").innerHTML;
            if(el.querySelector("input:checked") != null) {
              individual.questionanwser = el.querySelector("input:checked").value
            } else if(el.querySelector("[type=text]") != null) {
              individual.questionanwser = el.querySelector("[type=text]").value
            } else {
              individual.questionanwser = '';
            }
            individual.questionno = el.querySelector(".office-form-question-title").querySelector("span:first-child").innerHTML;
            data.push({
              questiontext: individual.questiontext,
              questionanwser: individual.questionanwser,
              questionno: individual.questionno
            });
      });
      this.data.data = data;
      this.data.user_id = this.user_id;
      console.log(data);
      this.postForm();
    },
    postForm() {
      var req = {
        what: "submitQuestionnaire",
        data: this.data
      };
      this.$request
        .makePostRequest(req)
        .then(response => {
          console.log(response.data.message);
          this.$swal.fire("Success", response.data.message, "success");
          this.data = [];
          $('#exampleModalLong').modal('hide');
        })
        .catch(error => {
          this.$swal.fire("Error", error.message, "error");
          $('#exampleModalLong').modal('hide');
          this.data = [];
        });
    },
  }
}

</script>

